/* eslint-disable react/prefer-stateless-function */
/* eslint-disable react/jsx-filename-extension */
import React from 'react'

import connect from '@vkontakte/vkui-connect'

import Lottie from 'lottie-react-web'

import * as Smoke from '../animation/smoke.json'

const texts = {
    china: ['глуять по великой', 'китайской стене'],
    pisa: ['фотографировать', 'башню в пизе'],
    everest: ['покорять вершины', 'эвереста'],
    london: ['смотреть на биг-бен', 'в лондоне'],
    paris: ['есть круассаны', 'в париже'],
    amsterdam: ['на гей-парад', 'в амстердаме'],
    taiga: ['драться с медведем', 'в тайге'],
    prague: ['за пивом и свининой', 'в прагу'],
    munich: ['на окоберфест', 'в мюнхен'],
    kaluga: ['гулять по улицам', 'калуги'],
    tbilisi: ['есть хинкали', 'в тбилиси'],
    mongolia: ['кататься по степям', 'монголии'],
    saintp: ['есть шаверму', 'в санкт-петербурге']
}

class Result extends React.Component {
    state = {
        ready: false,
    }

    componentDidMount() {
        setTimeout(() => this.setState({ ready: true }), 800)
    }

    componentDidUpdate(prevProps) {
        if (prevProps.result !== this.props.result || prevProps.friend.id !== this.props.friend.id) {
            this.setState({ ready: false })

            setTimeout(() => this.setState({ ready: true }), 800)
        }
    }

    handleRetryClick = () => {
        connect.send('VKWebAppGetFriends', {})
    }

    handleShareClick = () => {
        this.props.shareVideo()
    }

    render() {
        const { ready } = this.state
        const { user, friend, result } = this.props

        return (
            <div className={`result result--${result}`}>
                {!ready && <div className='animation'>
                    <Lottie
                        options={{
                            loop: true,
                            autoplay: true,
                            animationData: Smoke.default,
                            rendererSettings: {
                                preserveAspectRatio: 'xMidYMid slice'
                            },
                        }}
                        height='100%'
                        width='100%'
                        direction={-1}
                    />
                </div>}
                <div className={`result_content ${!ready ? 'hidden' : ''}`}>
                    <div className='logo' />
                    <div className='result_circles'>
                        <div className='result_circle circle'>
                            <div className='avatar' style={{ backgroundImage: `url(${user && (user.photo_200 || user.photo_max_orig)})` }} />
                        </div>
                        <div className='result_circle circle'>
                            <div className='avatar' style={{ backgroundImage: `url(${friend && (friend.photo_200 || friend.photo_max_orig)})` }} />
                        </div>
                    </div>
                    <div className='result_names'>
                        <div>Я и {friend.first_name}</div>
                    </div>
                    <div className='result_label parallelogram'>
                        <div>отправляемся...</div>
                    </div>
                    <div className='result_text parallelogram'>
                        <div>{texts[result][0]}</div>
                    </div>
                    <div className='result_text result_text--cutted parallelogram'>
                        <div>{texts[result][1]}</div>
                    </div>
                    <div className='share' onClick={this.handleShareClick} />
                    <div className='retry' onClick={this.handleRetryClick}>ещё раз</div>
                </div>
            </div>
        )
    }
}

export default Result
