import React from 'react'

import connect from '@vkontakte/vkui-connect'
import { View, Panel } from '@vkontakte/vkui'

import './App.css'

import Wrapper from './components/Wrapper'

const results = ['china', 'pisa', 'everest', 'london', 'paris', 'amsterdam', 'taiga', 'prague', 'munich', 'kaluga', 'tbilisi', 'mongolia', 'saintp']

class App extends React.Component {
    constructor(props) {
        super(props)
        // this.state = {
        //     block: 'result',
        //     user: { id: 9387646, first_name: 'Славик', last_name: 'Беляков', sex: 2, city: { id: 2, title: 'Санкт-Петербург' }, country: { id: 1, title: 'Россия' }, photo_100: 'https://sun9-30.userapi.com/c849020/v849020067/30b7f/GmZOQKJK1uA.jpg?ava=1', photo_max_orig: 'https://sun9-21.userapi.com/c849020/v849020067/30b7b/zLZOrrEByAc.jpg?ava=1', bdate: '6.9.1995', photo_200: 'https://sun9-8.userapi.com/c849020/v849020067/30b7e/Vd-E144O3PA.jpg?ava=1', timezone: 3 },
        //     friend: { id: 296749655, sex: 1, last_name: 'Белякова', first_name: 'Мия', photo_200: 'https://sun9-5.userapi.com/c855436/v855436501/c8fc4/nHSgyDD4FD8.jpg?ava=1' },
        //     result: results[Math.floor(Math.random() * results.length)],
        //     shared: false
        // }
        this.state = {
            block: 'home',
            user: null,
            friend: null,
            result: '',
            share: false
        }
    }

    componentDidMount() {
        connect.send('VKWebAppInit', {})

        connect.subscribe((res) => {
            // document.getElementById('debug').innerHTML += `\n${res.detail.type}`

            switch (res.detail.type) {
            case 'VKWebAppGetUserInfoResult':
                this.setState({ user: res.detail.data })
                break
            case 'VKWebAppGetFriendsResult':
                this.setState({
                    block: 'result',
                    friend: res.detail.data.users[0],
                    result: results[Math.floor(Math.random() * results.length)]
                })
                break
            default:
                break
            }
        })

        connect.send('VKWebAppGetUserInfo', {})

        connect.send('VKWebAppGetAuthToken', { app_id: 7105859, scope: 'friends,status' })
    }


    handleShareVideo = () => {
        this.setState({
            block: 'home',
            shared: true
        })
    }

    render() {
        const { block, user, friend, result, shared } = this.state

        return (
            <View activePanel='main'>
                <Panel id='main'>
                    <Wrapper
                        block={block}
                        user={user}
                        friend={friend}
                        result={result}
                        shared={shared}
                        shareVideo={this.handleShareVideo}
                    />
                </Panel>
            </View>
        )
    }
}

export default App
