/* eslint-disable react/jsx-filename-extension */

import React from 'react'

import connect from '@vkontakte/vkui-connect'

import Lottie from 'lottie-react-web'

import * as Planes from '../animation/planes.json'
import * as Smoke from '../animation/smoke.json'

class Home extends React.Component {
    state = {
        ready: false,
        start: false
    }

    componentDidMount() {
        setTimeout(() => this.setState({ ready: true }), 800)
    }

    handleStartClick = () => {
        this.setState({ start: true })

        setTimeout(() => connect.send('VKWebAppGetFriends', {}), 800)
    }

    render() {
        const { ready, start } = this.state
        const { user, shared } = this.props

        return (
            <div className={`home ${shared ? 'home--shared' : ''}`}>
                <div className='logo' />
                {(!ready || start) && <div className='animation'>
                    <Lottie
                        options={{
                            loop: false,
                            autoplay: true,
                            animationData: (!ready && Planes.default) || Smoke.default,
                            rendererSettings: {
                                preserveAspectRatio: 'xMidYMid slice'
                            }
                        }}
                        height='100%'
                        width='100%'
                    />
                </div>}
                <div className={`home_content ${(!ready || start) ? 'hidden' : ''}`}>
                    <h1>{!shared ? 'Узнай' : 'Готово!'}</h1>
                    <p>{!shared ? 'куда и с кем ты поедешь в путешествие' : 'Участвуй в конкурсе и выиграй путешествие'}</p>
                    {!shared && <div className='home_circles'>
                        <div className='home_circle circle'>
                            <div className='avatar' style={{ backgroundImage: `url(${user && (user.photo_200 || user.photo_max_orig)})` }} />
                        </div>
                        <span>+</span>
                        <div className='home_circle circle'>
                            <div className='avatar avatar--empty'>?</div>
                        </div>
                    </div>}
                    <div className={shared ? 'join' : 'start'} onClick={this.handleStartClick} />
                </div>
            </div>
        )
    }
}

export default Home
